// reset
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }


article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote {
  &:before, &:after {
    content: '';
    content: none; } }

q {
  &:before, &:after {
    content: '';
    content: none; } }

table {
  border-collapse: collapse;
  border-spacing: 0; }


// mixin
@mixin landscape() {
  @media all and (orientation:landscape) {
    @content; } }



body {
  background-color: pink;
  width: 100vw;
  height: 100vh;
  @include landscape {
    background-color: skyblue; } }

// container
#container {
  border: 1px solid #eee;
  width: 80vw;
  height: 60vw;
  @include landscape {
    width: 70vw;
    height: 70vw * 0.75; }
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-flow: row wrap; }

.square {
  margin: auto;
  width: 25%;
  height: calc(100%/3);
  position: relative; }

.squareImg {
  display: none;
  width: 20vw * 0.7;
  @include landscape {
    width: 70vw * 0.25 * 0.7; }
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &.show {
    display: block; } }


// drag and drop

.touchstart {
  opacity: 0.5;
  z-index: 2; }

.dragging {
  position: fixed; }
